<template>
    <div>
        <div>
            <search-form v-if="searchFlag" :onAdd="onAdd" :searchObj="searchObj" @searchList="searchList"/>
        </div>
        <el-table
                :data="list"
                style="width: 100%"
                @row-click="toCaseDispose">
            <el-table-column
                    prop="caseNum"
                    label="案号"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                    prop="caseProcessName"
                    label="当前环节"
                    width="100">
            </el-table-column>
            <el-table-column
                    prop="orgName"
                    label="部门"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                    prop="party"
                    label="当事人"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                    prop="crimeLocal"
                    label="案发地点"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column
                    prop="discoveryTime"
                    label="发现时间"
                    width="140">
                <template slot-scope="scope">
                    {{scope.row.discoveryTime | formatDate}}
                </template>
            </el-table-column>
            <el-table-column
                    prop="reasonName"
                    label="违法行为"
                    :show-overflow-tooltip="true">
            </el-table-column>
            <el-table-column align="center" label="操作" width="100">
                <template slot-scope="scope">
<!--                    查看案件功能暂不需要-->
<!--                    <el-tooltip :open-delay="500" content="查看" placement="top">-->
<!--                        <el-link @click.stop="seeFunction(scope.row)" icon="el-icon-search" type="success"></el-link>-->
<!--                    </el-tooltip>-->

                    <el-tooltip
                            :open-delay="500"
                            content="处理"
                            placement="top">
<!--                            点击跳转到案件处理-->
                        <el-link @click.stop="toCaseDispose(scope.row)" icon="el-icon-s-order" style="margin-left: 5px"
                                 type="warning"></el-link>
                    </el-tooltip>
                </template>

            </el-table-column>
        </el-table>
        <see-block :id="seeObj.id" :show.sync="seeObj.show"/>
        <add-block :show.sync="addObj.show" :title="addObj.title" @upDateList="getList" v-if="addObj.show"/>
        <i-pagination :pageNum.sync="form.pageNum" :pageSize.sync="form.pageSize" :total="total" @getData="getList"
                      style="margin-top:10px"/>
    </div>
</template>

<script>
    import searchForm from '../../../components/LjSearchForm'
    import seeBlock from './see'
    import addBlock from './add'
    import iPagination from '../../../components/LjPagination'
    import {formatDate} from "../../../until/wordBook";
    import {apiNormalCaseSearch,apiNormalCaseDetailGet} from "../../../api/case";

    export default {
        name: "generalCasePage",
        components: {searchForm, seeBlock, iPagination, addBlock},
        data() {
            return {
                searchFlag: true,
                form: {
                    keyWord: '',
                    pageNum: 1,
                    pageSize: 5
                },
                searchObj: [
                    {type: 'select', name: 'HandlingDepartment', options: [], label: '办案部门'},
                    {type: 'select', name: 'enforceId', options: [], label: '执法领域'},
                    {type: 'select', name: 'caseStatus', options: [], label: '案件状态'},
                    {
                        type: 'date',
                        dateType: 'daterange',
                        name: 'timeRange',
                        name1: 'startTime',
                        name2: 'endTime',
                        options: [],
                        label: '案发时间',
                        span: 12
                    },
                ],
                seeObj: {
                    show: false,
                    title: '查看一般案件'
                },
                addObj: {
                    show: false,
                    title: '新增一般案件'
                },
                total: 0,
                list: []
            }
        },
        methods: {
            searchList(data) {
                data.pageSize = this.form.pageSize;
                this.form = data;
                this.form.pageNum = 1;
                apiNormalCaseSearch(this.form).then(res => {
                    this.total = res.total;
                    this.list = res.data;
                })
            },
            onAdd() {
                this.addObj.show = true
            },
            //
            // seeFunction(data) {
            //     this.seeObj.id = data.dsId;
            //     this.seeObj.show = true
            // },
            getList() {
                this.loading = true;
                console.log(this.form)
                apiNormalCaseSearch(this.form).then(res => {
                    console.log(res)
                    this.total = res.total;
                    this.list = res.data;
                    this.loading = false;
                });
            },
            // 跳转到案件处理
            toCaseDispose(row) {
                let id = row.nmId;
                this.$router.push('/generalCaseDispose/'+id)
            },
            // 查询接口
            // toCaseDispose(row) {
            //     let id = row.nmId
            //     apiNormalCaseDetailGet(id).then(res => {
            //         console.log(res)
            //     })
            // },


            // 字典查询方法
            // this.$until.workBookApi(`${val}`).then(res => {
            //         return res;
            //     });
            // },
        },
        created() {
            this.form.pageNum = 1;
            this.form.pageSize = 10;
            apiNormalCaseSearch(this.form).then(res => {
                console.log(res);
                this.total = res.total;
                this.list = res.data;
            });
        },
        filters: {
            formatDate(value) {
                return formatDate(value, 'yy/MM/dd hh:mm')
            },
        },
    }
</script>

<style scoped>

</style>
