<template>
    <div>
        <el-dialog
                :close-on-click-modal="false"
                :visible="show"
                @close="closeDialog"
                append-to-body
                center
                title="案件详情"
                top="50px"
                width="1080px"
        >
            <el-row :gutter="10">
                <el-col :span="16">
                    <table class="gridTable" v-if="form">
                        <tr>
                            <td class="lj-background" colspan="4">
                                <b>基本信息</b>
                            </td>
                        </tr>
                        <tr>
                            <td colspan="4">案件信息：</td>
                        </tr>
                        <tr>
                            <td class="label">案件状态：</td>
                            <td>{{form.caseStatus }}</td>
                            <td class="label">案发时间：</td>
                            <td>{{form.crimeTime | formatDate}}</td>
                        </tr>
                        <tr>
                            <td class="label">主办人：</td>
                            <td>{{form.investigatorsName}}</td>
                            <td class="label">协办人：</td>
                            <td>{{form.coInvestigatorsName}}</td>
                        </tr>
                        <tr>
                            <td class="label">办案部门：</td>
                            <td>{{form.handlingDepartmentName}}</td>
                            <td class="label">街道：</td>
                            <td>{{form.cityName}}{{form.districtName}}{{form.streetOfficeName}}</td>
                        </tr>
                        <tr>
                            <td class="label">案件来源：</td>
                            <td>{{caseSource(form.caseSource)}}</td>
                            <td class="label">案发地点：</td>
                            <td>{{form.crimeLocal}}</td>
                        </tr>

                        <tr>
                            <td colspan="4">违法信息：</td>
                        </tr>
                        <tr>
                            <td class="label">执法领域：</td>
                            <td>{{form.enforceName}}</td>
                            <td class="label">违法类型：</td>
                            <td>{{form.illegalTypesName}}</td>
                        </tr>
                        <tr>
                            <td class="label">案由：</td>
                            <td>{{form.reasonName}}</td>
                            <td class="label"></td>
                            <td></td>
                        </tr>
                        <tr>
                            <td class="label">依据条例内容：</td>
                            <td>{{form.accordingRegulationsContent}}</td>
                            <td class="label">违反条例内容：</td>
                            <td>{{form.violationRegulationsContent}}</td>
                        </tr>

                        <tr>
                            <td colspan="4">当事人信息：</td>
                        </tr>
                        <tr>
                            <td class="label">当事人类型：</td>
                            <td>{{form.partyType == 1?'个人':'单位'}}</td>
                            <td class="label">{{form.partyType == 2 ? '当事公司：' :'当事人：'}}</td>
                            <td>{{form.party}}</td>
                        </tr>
                        <tr>
                            <td class="label">{{form.partyType == 2 ? '统一信用码：' :'身份证：'}}</td>
                            <td>{{form.partyIdcard}}</td>
                            <td class="label">{{form.partyType == 2 ? '法人':''}}</td>
                            <td>{{form.partyType == 2 ? form.legalPerson : ''}}</td>
                        </tr>

                        <tr>
                            <td class="el-background" colspan="4">
                                <b>处罚信息</b>
                            </td>
                        </tr>
                        <tr>
                            <td class="label">决定书编号：</td>
                            <td>{{form.pmDecisionCode}}</td>
                            <td class="label">责令改正：</td>
                            <td>{{form.correctionMethod == 1 ? '立即':''}}</td>
                        </tr>
                        <!--                        <tr>-->
                        <!--                            <td class="label">整改最后期限：</td>-->
                        <!--                            <td>{{form.correctionLastTime}}</td>-->
                        <!--                            <td class="label">整改结果：</td>-->
                        <!--                            <td>{{form.correctionResult == 1 ? '已改':'未改'}}</td>-->
                        <!--                        </tr>-->
                        <tr>
                            <td class="label">处罚类型：</td>
                            <td>
                                <span class="lj-punish" v-if="form.isProcessingWarning == 1">警告</span>
                                <span class="lj-punish" v-if="form.isProcessingPunish == 1">罚款</span>
                            </td>
                            <td class="label">履行时间：</td>
                            <td>{{form.performanceTime| formatDate}}</td>
                        </tr>
                        <tr v-if="form.isProcessingPunish">
                            <td class="label">罚款金额：</td>
                            <td>{{form.penaltyAmount}}</td>
                            <td class="label">罚款方式：</td>
                            <td>{{form.paymentMethod == 1 ? '当场':(form.paymentMethod == 2 ? '银行':'')}}</td>
                        </tr>

                        <tr v-if="form.paymentMethod == 2">
                            <td class="label">缴款银行：</td>
                            <td>{{form.payingBank == 1 ? '建设':(form.payingBank == 2 ? '工商' : '')}}</td>
                            <td class="label">缴款单据编号：</td>
                            <td>{{form.paymentReceiptNumber}}</td>
                        </tr>
                    </table>
                </el-col>
                <el-col :span="8">
                    <el-card :key="item.typeName" class="box-card" v-for="item in documentList">
                        <div class="clearfix" slot="header">
                            <span>{{item.typeName}}</span>
                        </div>
                        <div class="no-block" v-if="filterList(item.dtId).length < 1">暂无文书</div>
                        <div :key="data.value" class="text item" v-for="data in filterList(item.dtId)">
                            <i class="el-icon-tickets"></i>
                            {{data.fileName}}
                            <div class="doc-see">
                                <el-tooltip :open-delay="500" content="查看当前文书" placement="top">
                                    <el-link @click.stop="showImg(data)" icon="el-icon-search" type="primary"></el-link>
                                </el-tooltip>
                                <el-tooltip :open-delay="500" class="download-doc" content="下载当前文书" placement="top">
                                    <el-link @click.stop="dowImg(data)" icon="el-icon-download"
                                             type="success"></el-link>
                                </el-tooltip>
                            </div>
                        </div>
                    </el-card>
                </el-col>
            </el-row>

            <span class="dialog-footer" slot="footer">
        <el-button @click="closeDialog" size="small" type="info">
          <i class="iconfont icon-back"></i>关 闭
        </el-button>
      </span>
        </el-dialog>
    </div>
</template>
<script>
    import {apiDocumentTypeSearch,
            apiFileDownLoad,
            apiNormalCaseDetailGet,
            seeImgApi} from "../../../api/case";
    import {formatDate} from "../../../until/wordBook";

    export default {
        name: "edit",
        props: {
            show: {
                type: Boolean,
                default: false
            },
            id: {
                type: String,
                default: ""
            }
        },
        data() {
            return {
                form: {},
                documentList: [],
                caseSourceList: [],
                fileList: []
            };
        },
        computed: {
            filterList() {
                return function (o) {
                    return this.fileList.filter(i => {
                        return i.dtId == o;
                    });
                };
            }
        },
        methods: {
            dowImg(data) {
                apiFileDownLoad(data.fileId);
            },
            showImg(data) {
                window.open(seeImgApi + data.fileId);
            },
            closeDialog() {
                this.$emit("update:show", false);
            },
            //弹框弹出时获取data给form
            initPage() {
                apiNormalCaseDetailGet(this.id).then(res => {
                    console.log(res)
                    // this.form = res.simpleCaseData;
                    // this.fileList = res.fileData;
                    // this.documentList = res.data;
                });
            },
            caseSource(value) {
                let obj = this.caseSourceList.find(i => {
                    return i.dictValue == value;
                });
                if (obj) {
                    return obj.dictName;
                } else {
                    return "";
                }
            }
        },
        filters: {
            formatDate(val) {
                return formatDate(val, "yyyy-MM-dd hh:mm");
            }
        },
        created() {
            this.$until.workBookApi("caseSource").then(res => {
                this.caseSourceList = res;
            });
            // apiDocumentTypeSearch({pageNum: 1, pageSize: 1000}).then(res => {
            //     this.documentList = res.data;
            // });
        },
        //监听，如果show为true,则执行initPage()
        watch: {
            show(value) {
                if (value) {
                    this.initPage();
                }
            }
        }
    };
</script>
<style lang='less' scoped>
    .alert_wrop {
        padding: 10px;
        display: flex;
        justify-content: space-between;
    }

    .selectwidth {
        width: 100%;
    }

    .box-card {
        margin: 10px 0;
        line-height: 2em;
    }

    .no-block {
        text-align: center;
        font-size: 20px;
        color: #cccccc;
        line-height: 60px;
    }

    table {
        .gridTable;
        border-collapse: collapse;
        width: 100%;

        td {
            width: 35%;

            &.label {
                width: 300px;
            }
        }
    }

    .lj-background {
        background: #e5e7f8;
    }

    .lj-punish {
        padding: 0 5px;
    }

    .download-doc {
        margin-left: 5px;
    }

    .doc-see {
        float: right;
    }
</style>


